.tabcontainer {
    background-color: #100a1c;
    color: white;
}

.test {
    width: 25vw;
}
.tabs {
    background-color: #1a1124;
}
.usernameExtended {
    margin-top: auto;
    margin-bottom: auto;
    background-color: #1a1124;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    font-size: 36px;
    margin-left: 0.1em;
}
.inline {
    display: inline;
}
.usernameBar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    background-color: #1a1124;
    width: 88%;
    margin-left: auto;
    margin-right: auto;
}
.userRank {
    display: -ms-flexbox;
    display: flex;
    position: relative;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: 0;
    padding-top: 10px;
}
.userRank span {
    display: -ms-flexbox;
    display: flex;

    padding: 0;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 0.8em;
    margin-top: auto;
    margin-bottom: auto;
}
.userRank h5 {
    margin-left: 3px;
    display: -ms-flexbox;
    display: flex;
    margin-top: auto;
    margin-bottom: auto;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 0.9em;
    color: #7c5e9c;
}

.wrapperusername {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    background-color: #1a1124;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    height: 20px;
}
.wrapperusername2 {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    background-color: #1a1124;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    height: 20px;
}
.inkbarlower {
    display: block;
    position: absolute;

    margin: 0;
    padding: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
}

.userInfoArea {
    background-color: #1a1124;
    height: 16%;
}
.navArea {
    background-color: #1a1124;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-direction: row;
        flex-direction: row;
    height: 5%;
}
.navBar {
    display: -ms-flexbox;
    display: flex;
    width: 88%;
    max-height: 28px;
    margin-left: auto;
    margin-right: auto;
    -ms-flex-direction: row;
        flex-direction: row;
    background-color: #1a1124;
}
.push {
    margin-left: auto;
}

.navMenu {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 0.65em;
    margin-left: 40px;
    margin-top: 10px;
    margin-bottom: auto;
    color: rgba(255, 255, 255, 0.2);
    letter-spacing: 1px;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;

    cursor: pointer;
}
.navMenu:hover {
    color: rgba(255, 255, 255, 0.5);
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
}
.menuLogo {
    opacity: 0.7;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
    cursor: pointer;
    z-index: 100;
    margin-top: 5px;
    margin-bottom: auto;
}

.menuLogo:hover {
    opacity: 1;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
    cursor: pointer;
    z-index: 100;
}

.searchIcon {
    position: relative;
    width: 25px;
    height: 25px;
    opacity: 0.3;
    display: -ms-flexbox;
    display: flex;
    margin-left: auto;
    margin-right: 0;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
    cursor: pointer;
    margin-bottom: auto;
    margin-top: 5px;
}
.searchIcon:hover {
    opacity: 1;
    cursor: pointer;
}



